.dataverify .toupdate {
  display: block;
  width: 6.96rem;
  height: 0.7rem;
  line-height: 0.7rem;
  text-align: center;
  margin: 0.27rem auto 0;
  background: #ccac6f;
  color: #fff;
  border-radius: 0.35rem;
  letter-spacing: 1px;
  font-size: 0.3rem;
  letter-spacing: 2px;
  position: fixed;
  bottom: 0.36rem;
  left: 50%;
  -webkit-transform: translateX(-50%);
  z-index: 999;
}



