}
.records.journal .records-date-select .select-btn .mint-button--small {
    height: 31px;
}
.records.journal .total-money {
  background-color: #354151;
  color: #656c76;
  padding: 0.75rem;
}
.records.journal .total-money .money {
    color: #fff;
    font-size: 3rem;
}



