.goshoppingcart {
  width: 100%;
  height: 0.96rem;
  background: #fff;
  border-top: 1px solid #dddddd;
  box-sizing: border-box;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 999999;
}
.goshoppingcart .con {
    width: 0.96rem;
    height: 0.96rem;
    border-radius: 50%;
    border: 1px solid #dddddd;
    background: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    position: relative;
    -webkit-transform: translate(0.26rem, -0.21rem);
            transform: translate(0.26rem, -0.21rem);
}
.goshoppingcart .con .goodnum {
      width: 0.52rem;
      height: 0.3rem;
      background: #ccac6f;
      border-radius: 0.25rem;
      text-align: center;
      line-height: 0.3rem;
      color: #fff;
      position: absolute;
      top: -0.1rem;
      right: -0.1rem;
}
.goshoppingcart .shops {
    color: #666666;
    position: absolute;
    right: 2.16rem;
    top: 0;
    height: 100%;
    width: 3.7rem;
    opacity: 0.6;
}
.goshoppingcart .shops span {
      display: block;
}
.goshoppingcart .shops span:first-child {
      font-size: 0.28rem;
      position: absolute;
      top: 0.1rem;
}
.goshoppingcart .shops span:last-child {
      font-size: 0.22rem;
      position: absolute;
      bottom: 0.17rem;
}
.goshoppingcart .btn {
    width: 2.16rem;
    height: 100%;
