      width: 100%;
      height: 0.4rem;
      position: relative;
      text-align: center;
      line-height: 1.3rem;
      font-size: 0.34rem;
      letter-spacing: 2px;
      color: #CCAC6F;
}
.message .win .head .close {
        width: 1rem;
        height: 1rem;
        position: absolute;
        right: 0;
        top: 0;
        color: #333;
        line-height: 1rem;
}
.message .win .foot {
      display: block;
      width: 6.96rem;
      height: 0.7rem;
